.stepper {
  display: flex;
  position: relative;
  top: 5vh;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border-radius: 15px;
  height: 3vh;
  background: #aab78f;

  .stepper-item {
    position: absolute;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    background-color: #293B14FF;
    color: white;
  }
}