a {
  text-decoration: none;
  margin: 10px;

  .product-card {
    display: grid;
    grid-template-rows: 70% 30%;
    border-radius: 15px;
    padding: 0 20px;
    width: 250px;
    height: 300px;
    color: $darkGreen;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    .product-infos {
      backdrop-filter: blur(7px);
      text-decoration: none;
      padding-top: 10px;

      h5 {
        font-size: 1.1rem;
        padding: 5px 0;
      }

      p {
        font-size: 1.7rem;
      }
    }

  }
}
