main.products-listing-section {

  .title {
    display: flex;
    justify-content: center;
    padding: 40px;

    h1 {
      font-family: $regularfont;
      color: $darkGreen;
      font-weight: bolder;
      font-size: 4rem;
    }
  }

  .products-listing {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;

    .product {
      width: 30%;
      height: 30vh;
      background-color: $white;
      border-radius: 5px;
      padding: 5px;
    }
  }

  .hamburger {
    z-index: 1000;
    -webkit-box-shadow: 0px 0px 30px -6px #000000;
    box-shadow: 0px 0px 30px -6px #000000;
    display: inline-block;
    background: $white;
    border-radius: 100%;
    cursor: pointer;
    border: none;
    padding: 20px;
    position: fixed;
    top: 40px;
    left: 40px;

    &.open {
      background: $lightGreen;
    }

    .bar {
      display: block;
      width: 35px;
      height: 4px;
      margin: 6px auto;
      transition: all 0.3s ease-in-out;
      background-color: #333;
    }

    &.open .bar:nth-child(1) {
      transform: translateY(10px) rotate(45deg);
    }

    &.open .bar:nth-child(2) {
      opacity: 0;
    }

    &.open .bar:nth-child(3) {
      transform: translateY(-10px) rotate(-45deg);
    }
  }

  .sidebar {
    width: 25%;
    position: fixed;
    top: 0;
    left: -25%;
    height: 100%;
    background-color: $white;
    box-shadow: 2px 0 5px rgba(0,0,0,0.5);
    transition: left 0.3s ease;
    padding: 11% 40px;
    overflow-y: scroll;
    z-index: 999;

    &.open {
      left: 0;
    }
  }

  .sidebar-element {
    padding: 20px 0;


    .sidebar-title {
      font-size: 2rem;
      font-weight: 600;
      padding: 10px 0;
    }

    .sidebar-children {
      display: flex;
      align-items: center;
      padding: 5px 0;
    }

    .sidebar-children-label {
      font-size: 1.2rem;
      cursor: pointer;
    }
  }
}




