.question {
  padding-top: 10vh;
  font-size: 1.4rem;

  .question-libelle {
    text-align: center;
  }

  .choix-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    flex-direction: column;

    .choix {
      user-select: none;
      width: 25rem;
      padding: 10px;
      font-size: 1.2rem;
      height: 100px;
      background: rgba(255, 255, 255, 0.5);
      text-align: center;
      align-content: center;
      border-radius: 10px;
      cursor: pointer;
    }

    .choix-checkbox {
      visibility: hidden;

      &:checked + .choix {
        background: white;
      }
    }

    .custom-input {
      width: 100%;
      height: 100%;
      font-size: 1.2rem;
      padding: 10px;
      border: none;
      background: rgba(255, 255, 255, 0.5);

      &:focus {
        border: none;
      }
    }
  }


}