// polices //
@font-face {
  font-family: "cousine";
  src: url(../fonts/Cousine-Regular.ttf);
}

// variables //
$regularfont: "cousine", sans-serif;

$lightGreen: #dae3c6;
$darkGreen: #293B14;
$white: #F3F3F4;

// code général //
::selection {
  background: $darkGreen;
  color: $white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $regularfont;
}

html {
  scroll-behavior: smooth;
}

body {
  background: $lightGreen;
  position: relative;
}

main {
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
}
