main.home-section {
  background-color: $lightGreen;

  .title {
    display: flex;
    justify-content: center;
    padding: 40px;

      h1 {
        font-family: $regularfont;
        color: $darkGreen;
        font-weight: bolder;
        font-size: 4rem;
      }
  }

  .products-listing {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;

      .product {
        width: 30%;
        height: 30vh;
        background-color: $white;
        border-radius: 5px;
        padding: 5px;
      }
  }

  .products-link {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    color: $darkGreen;
  }

  .diagnostic-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $darkGreen;
    color: $lightGreen;
    margin: 5vh 0;
    position: absolute;
    left: 0;
    width: 100vw;
    padding: 10vh 10vw;

    h2 {
      font-size: 2rem;
    }

    .button-diag {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 5px 30px;
      cursor: pointer;
      height: 55px;
      position: relative;
      top: 3%;
      right: -3.5%;
      background: $lightGreen;
      border: none;
      border-radius: 30px;
      color: $darkGreen;
      text-decoration: none;

      svg {
        width: 20px;
        height: 20px;
        margin-left: 10px;
        transform: rotate(320deg);
        fill: $darkGreen;
        transition: 200ms all ease-in-out;
      }

      &:hover {
        svg {
          transform: translate(7px, -4px) rotate(320deg);
        }
      }

    }
  }
}