@font-face {
  font-family: "cousine";
  src: url(../fonts/Cousine-Regular.ttf);
}
::selection {
  background: #293B14;
  color: #F3F3F4;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "cousine", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #dae3c6;
  position: relative;
}

main {
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
}

main.home-section {
  background-color: #dae3c6;
}
main.home-section .title {
  display: flex;
  justify-content: center;
  padding: 40px;
}
main.home-section .title h1 {
  font-family: "cousine", sans-serif;
  color: #293B14;
  font-weight: bolder;
  font-size: 4rem;
}
main.home-section .products-listing {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
main.home-section .products-listing .product {
  width: 30%;
  height: 30vh;
  background-color: #F3F3F4;
  border-radius: 5px;
  padding: 5px;
}
main.home-section .products-link {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  color: #293B14;
}
main.home-section .diagnostic-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #293B14;
  color: #dae3c6;
  margin: 5vh 0;
  position: absolute;
  left: 0;
  width: 100vw;
  padding: 10vh 10vw;
}
main.home-section .diagnostic-section h2 {
  font-size: 2rem;
}
main.home-section .diagnostic-section .button-diag {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 30px;
  cursor: pointer;
  height: 55px;
  position: relative;
  top: 3%;
  right: -3.5%;
  background: #dae3c6;
  border: none;
  border-radius: 30px;
  color: #293B14;
  text-decoration: none;
}
main.home-section .diagnostic-section .button-diag svg {
  width: 20px;
  height: 20px;
  margin-left: 10px;
  transform: rotate(320deg);
  fill: #293B14;
  transition: 200ms all ease-in-out;
}
main.home-section .diagnostic-section .button-diag:hover svg {
  transform: translate(7px, -4px) rotate(320deg);
}

main.products-listing-section .title {
  display: flex;
  justify-content: center;
  padding: 40px;
}
main.products-listing-section .title h1 {
  font-family: "cousine", sans-serif;
  color: #293B14;
  font-weight: bolder;
  font-size: 4rem;
}
main.products-listing-section .products-listing {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
main.products-listing-section .products-listing .product {
  width: 30%;
  height: 30vh;
  background-color: #F3F3F4;
  border-radius: 5px;
  padding: 5px;
}
main.products-listing-section .hamburger {
  z-index: 1000;
  -webkit-box-shadow: 0px 0px 30px -6px #000000;
  box-shadow: 0px 0px 30px -6px #000000;
  display: inline-block;
  background: #F3F3F4;
  border-radius: 100%;
  cursor: pointer;
  border: none;
  padding: 20px;
  position: fixed;
  top: 40px;
  left: 40px;
}
main.products-listing-section .hamburger.open {
  background: #dae3c6;
}
main.products-listing-section .hamburger .bar {
  display: block;
  width: 35px;
  height: 4px;
  margin: 6px auto;
  transition: all 0.3s ease-in-out;
  background-color: #333;
}
main.products-listing-section .hamburger.open .bar:nth-child(1) {
  transform: translateY(10px) rotate(45deg);
}
main.products-listing-section .hamburger.open .bar:nth-child(2) {
  opacity: 0;
}
main.products-listing-section .hamburger.open .bar:nth-child(3) {
  transform: translateY(-10px) rotate(-45deg);
}
main.products-listing-section .sidebar {
  width: 25%;
  position: fixed;
  top: 0;
  left: -25%;
  height: 100%;
  background-color: #F3F3F4;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  transition: left 0.3s ease;
  padding: 11% 40px;
  overflow-y: scroll;
  z-index: 999;
}
main.products-listing-section .sidebar.open {
  left: 0;
}
main.products-listing-section .sidebar-element {
  padding: 20px 0;
}
main.products-listing-section .sidebar-element .sidebar-title {
  font-size: 2rem;
  font-weight: 600;
  padding: 10px 0;
}
main.products-listing-section .sidebar-element .sidebar-children {
  display: flex;
  align-items: center;
  padding: 5px 0;
}
main.products-listing-section .sidebar-element .sidebar-children-label {
  font-size: 1.2rem;
  cursor: pointer;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 15px;
  margin: 20px;
  padding: 20px;
  overflow: hidden;
  box-shadow: 0 8px 60px -8px hsl(210, 14%, 80%);
  border: 1px solid rgb(213, 218, 223);
}

.cover {
  height: 200px;
  border-radius: 8px;
  overflow: hidden;
}

.cover img {
  display: block;
  width: 100%;
  opacity: 0.95;
}

/* add to img on load event */
.animate {
  animation: animate 1s ease;
}

@keyframes animate {
  from {
    transform: rotateX(20deg);
    opacity: 0;
  }
  to {
    transform: rotateX(0deg);
    opacity: 1;
  }
}
.content {
  position: relative;
  color: #333;
  margin-top: 8px;
}

.content small {
  display: block;
  margin: 8px 0;
  color: grey;
}

/* skeleton elements */
.skeleton {
  background: linear-gradient(90deg, hsl(210, 15%, 88%), hsl(210, 15%, 95%), hsl(210, 15%, 88%));
  background-size: 200%;
  animation: loader 1s infinite reverse;
}

h2.skeleton {
  width: 95%;
  height: 1em;
}

small.skeleton {
  height: 1rem;
  width: 4rem;
}

p.skeleton {
  height: 3em;
}

.loader-genres {
  display: flex;
}

.loader-genres small {
  margin-right: 10px;
}

/* skeleton animation */
@keyframes loader {
  from {
    background-position: -100% 0;
  }
  to {
    background-position: 100% 0;
  }
}
a {
  text-decoration: none;
  margin: 10px;
}
a .product-card {
  display: grid;
  grid-template-rows: 70% 30%;
  border-radius: 15px;
  padding: 0 20px;
  width: 250px;
  height: 300px;
  color: #293B14;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
a .product-card .product-infos {
  backdrop-filter: blur(7px);
  text-decoration: none;
  padding-top: 10px;
}
a .product-card .product-infos h5 {
  font-size: 1.1rem;
  padding: 5px 0;
}
a .product-card .product-infos p {
  font-size: 1.7rem;
}

.stepper {
  display: flex;
  position: relative;
  top: 5vh;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border-radius: 15px;
  height: 3vh;
  background: #aab78f;
}
.stepper .stepper-item {
  position: absolute;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  background-color: #293b14;
  color: white;
}

.question {
  padding-top: 10vh;
  font-size: 1.4rem;
}
.question .question-libelle {
  text-align: center;
}
.question .choix-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  flex-direction: column;
}
.question .choix-box .choix {
  user-select: none;
  width: 25rem;
  padding: 10px;
  font-size: 1.2rem;
  height: 100px;
  background: rgba(255, 255, 255, 0.5);
  text-align: center;
  align-content: center;
  border-radius: 10px;
  cursor: pointer;
}
.question .choix-box .choix-checkbox {
  visibility: hidden;
}
.question .choix-box .choix-checkbox:checked + .choix {
  background: white;
}
.question .choix-box .custom-input {
  width: 100%;
  height: 100%;
  font-size: 1.2rem;
  padding: 10px;
  border: none;
  background: rgba(255, 255, 255, 0.5);
}
.question .choix-box .custom-input:focus {
  border: none;
}

